








	export default {
		data() {
			return {
				imgList:[],
// 				page:1,
// 				pageSize:10
			}
		},
		methods:{
			
		},
		onLoad() {
			var token = (uni.getStorageSync('user_info')).token;
			var page = 1;
			var pageSize = 10;
			console.log(token)
			uni.request({
				url:'http://bus.liebianzhe.com/api/user/np_video',
				data:{
					token:token,
					page:page,
					pageSize:pageSize
				},
				success: (res) => {
					console.log(res)
					if(res.data.code==1){
						this.imgList = res.data.data;
						console.log(this.imgList)
					}
				},
				fail: (res) => {
					uni.showToast({
						title:'网络请求失败'
					})
				}
			})
		}
	}
